import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Event from '../../ui/Event';
import { roomShape, eventsShape } from '../../../intl/shapes';
import triggerModal from '../../../action-creators/pageReducer/triggerModal';
import setEventForEditing from '../../../action-creators/eventsReducer/setEventForEditing';
import triggerSelected from '../../../action-creators/roomsReducer/triggerSelected';
import resetRoom from '../../../action-creators/eventsReducer/resetRoom';
import { TRIGGER_EDIT_MODAL } from '../../../consts/pageReducer';
import './EventRow.scss';

class EventRow extends Component {
  static propTypes = {
    room: roomShape.isRequired,
    events: eventsShape.isRequired,
    openingHours: PropTypes.string.isRequired,
    monthsValue: PropTypes.arrayOf(PropTypes.string).isRequired,
    setEventForEditing: PropTypes.func.isRequired,
    triggerModal: PropTypes.func.isRequired,
    triggerSelected: PropTypes.func.isRequired,
    resetRoom: PropTypes.func.isRequired,
    closingHours: PropTypes.string.isRequired,
    isMobile: PropTypes.bool.isRequired,
  }

  getWidth = () => {
    const { openingHours, closingHours } = this.props;

    return `${(closingHours - openingHours) * 155}px`;
  }

  getEventDate = (date) => {
    const { monthsValue } = this.props;
    const { startingDate, startingMonth } = date;

    return `${startingDate} ${monthsValue[startingMonth]}`;
  }

  getEventTime = (time) => {
    const {
      startingHours, startingMinutes, endingHours, endingMinutes,
    } = time;

    return `${startingHours}:${startingMinutes} – ${endingHours}:${endingMinutes}`;
  }

  getEventOffsetLeft = (startingHours, startingMinutes, openingHours) => {
    if (Number(startingHours) === 8) {
      return 10; // shitty styling fix
    }

    return (startingHours - openingHours) * 155 + Number(startingMinutes / 60 * 155) + 1;
  }

  getEventWidth = ({ startingHours, startingMinutes, endingHours, endingMinutes }) => {
    if (Number(startingHours) === 8) {
      return (endingHours - startingHours) * 155 + (
        (endingMinutes / 60 * 155) - (startingMinutes / 60 * 155)
      ) - 8; // +1 shitty styling fix
    }

    return (endingHours - startingHours) * 155 + (
      (endingMinutes / 60 * 155) - (startingMinutes / 60 * 155)
    );
  }

  openEditModal = (event) => {
    const {
      setEventForEditing, triggerModal, triggerSelected, resetRoom,
    } = this.props;

    resetRoom();
    setEventForEditing(event);
    triggerSelected(event.room.id);
    triggerModal(TRIGGER_EDIT_MODAL);
  }

  render() {
    const { room, events, openingHours, closingHours, isMobile } = this.props;
    const containerWidth = this.getWidth(openingHours, closingHours);

    return (
      <div className="event-row" style={{ width: containerWidth }}>
        {events.map((event) => {
          const offsetLeft = this.getEventOffsetLeft(
            event.time.startingHours,
            event.time.startingMinutes,
            openingHours,
          );

          const width = this.getEventWidth(event.time);
          const date = this.getEventDate(event.date);
          const time = this.getEventTime(event.time);

          return (
            <Event
              key={event.id}
              name={event.name}
              roomName={room.name}
              users={event.users}
              openEditModal={() => {
                this.openEditModal(event);
              }}
              offsetLeft={offsetLeft}
              width={width}
              date={date}
              time={time}
              isMobile={isMobile}
            />
          );
        })}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  openingHours: state.pageReducer.openTime.hours,
  closingHours: state.pageReducer.closeTime.hours,
  monthsValue: state.dateReducer.monthsValue,
  isMobile: state.pageReducer.isMobile,
});

const mapDispatchToProps = {
  triggerModal,
  setEventForEditing,
  triggerSelected,
  resetRoom,
};

export default connect(mapStateToProps, mapDispatchToProps)(EventRow);
