import { REARRANGE_ROOMS } from '../../consts/mainContentReducer';

const sortByFloor = (rooms) => {
  if (rooms.length < 2) {
    return rooms;
  }

  const pivotPosition = Math.floor(Math.random() * rooms.length);
  const pivot = rooms[pivotPosition];
  const less = rooms.filter(elem => elem.floor < pivot.floor);

  const greater = rooms.filter(elem => elem.floor > pivot.floor);

  return [...sortByFloor(greater), pivot, ...sortByFloor(less)];
};

const rearrangeRooms = (rooms) => {
  const rearrangedRooms = rooms.reduce((roomsToRearrange, { floor, ...otherKeys }) => {
    const isUniqueEvent = roomsToRearrange.every(room => floor !== room.floor);

    if (!isUniqueEvent) {
      return roomsToRearrange.map(rearrangedRoom => (
        rearrangedRoom.floor === floor
          ? { ...rearrangedRoom, rooms: [...rearrangedRoom.rooms, otherKeys] }
          : rearrangedRoom
      ));
    }

    return [...roomsToRearrange, { floor, rooms: [otherKeys] }];
  }, []);

  return { type: REARRANGE_ROOMS, rearrangedRooms: sortByFloor(rearrangedRooms) };
};

export default rearrangeRooms;
