import { FILL_TIMESTRIPE_ARRAY } from '../../consts/mainContentReducer';

const fillTimeStripeArray = (openTime, closeTime) => {
  const newTimeStripeArray = [];

  for (let i = openTime; i < closeTime; i++) {
    newTimeStripeArray.push(i);
  }

  return {
    type: FILL_TIMESTRIPE_ARRAY,
    timeStripeArray: newTimeStripeArray,
  };
};

export default fillTimeStripeArray;
