import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { injectIntl, intlShape } from 'react-intl';
import onClickOutside from 'react-onclickoutside';
import ArrowBtn from '../../ui/ArrowBtn';
import Calendar from '../Calendar';
import Headline from '../../ui/Headline';
import updateCurrentDate from '../../../action-creators/dateReducer/updateCurrentDate';
import updateBtnState from '../../../action-creators/dateReducer/updateBtnState';
import updateNewEventDate from '../../../action-creators/eventsReducer/updateDate';
import triggerCalendar from '../../../action-creators/dateReducer/triggerCalendar';
import './DatePicker.scss';

class DatePicker extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    date: PropTypes.number.isRequired,
    month: PropTypes.number.isRequired,
    prevDayBtnDisabled: PropTypes.bool.isRequired,
    updateBtnState: PropTypes.func.isRequired,
    updateNewEventDate: PropTypes.func.isRequired,
    updateCurrentDate: PropTypes.func.isRequired,
    triggerCalendar: PropTypes.func.isRequired,
    calendarVisible: PropTypes.bool.isRequired,
    days: PropTypes.arrayOf(PropTypes.string).isRequired,
    monthsShort: PropTypes.arrayOf(PropTypes.string).isRequired,
  }

  handleClickOutside = () => {
    const { triggerCalendar, calendarVisible } = this.props;

    if (calendarVisible) {
      triggerCalendar();
    }
  }

  getDate = (date, month) => {
    const { intl: { formatMessage }, days, monthsShort } = this.props;
    const todayMsg = formatMessage({ id: 'datePicker.todayMsg' });
    const tomorrowMsg = formatMessage({ id: 'datePicker.tomorrowMsg' });
    const currentDate = new Date();
    const todayDate = currentDate.getDate();
    const todayMonth = currentDate.getMonth();
    const specifiedDate = new Date(currentDate.getFullYear(),
      month, date).getDay();
    const nextDay = new Date(currentDate.getFullYear(), todayMonth, todayDate + 1).getDate();

    let dayOfTheWeek;

    switch (date) {
      case todayDate:
        if (todayMonth === month) {
          dayOfTheWeek = todayMsg;
        }
        break;

      case nextDay:
        dayOfTheWeek = tomorrowMsg;
        break;

      default:
        dayOfTheWeek = days[specifiedDate];
    }

    return `${date} ${monthsShort[month]} · ${dayOfTheWeek}`;
  }

  handleNext = () => {
    const {
      updateBtnState, date, month, updateNewEventDate, updateCurrentDate,
    } = this.props;
    const isLastDayInNextMonth = new Date(
      new Date().getFullYear(), month + 1, 0,
    ).getDate() === date;

    if (isLastDayInNextMonth) {
      updateCurrentDate(month + 1, 1);
      updateNewEventDate(month + 1, 1);
    } else {
      updateCurrentDate(month, date + 1);
      updateNewEventDate(month, date + 1);
    }

    updateBtnState();
  }

  handlePrev = () => {
    const {
      updateBtnState, date, month, updateNewEventDate, updateCurrentDate,
    } = this.props;
    const lastDayInMonth = new Date(new Date().getFullYear(), month, 0).getDate();
    const shouldChangeMonth = new Date(new Date().getFullYear(), month, 1).getDate() === date;

    if (shouldChangeMonth) {
      updateCurrentDate(month - 1, lastDayInMonth);
      updateNewEventDate(month - 1, lastDayInMonth);
    } else {
      updateCurrentDate(month, date - 1);
      updateNewEventDate(month, date - 1);
    }

    updateBtnState();
  }

  render() {
    const {
      date,
      month,
      prevDayBtnDisabled,
      calendarVisible,
      triggerCalendar,
      intl: {
        formatMessage,
      },
    } = this.props;
    const rightArrowBtnLabel = formatMessage({ id: 'datePicker.rightArrowBtnLabel' });
    const leftArrowBtnLabel = formatMessage({ id: 'datePicker.rightArrowBtnLabel' });

    return (
      <div>
        <div className="date-picker">
          <ArrowBtn
            rotation="left"
            disabled={prevDayBtnDisabled}
            onClick={this.handlePrev}
            className="date-picker__btn-left"
            aria-label={leftArrowBtnLabel}
          />

          <Headline className="date-picker__headline" onClick={triggerCalendar}>
            {this.getDate(date, month)}
          </Headline>

          <ArrowBtn
            rotation="right"
            onClick={this.handleNext}
            className="date-picker__btn-right"
            aria-label={rightArrowBtnLabel}
          />
        </div>

        {calendarVisible
          && <Calendar triggerCalendar={triggerCalendar} />
        }
      </div>
    );
  }
}

const mapStateToProps = state => ({
  date: state.dateReducer.date,
  month: state.dateReducer.month,
  prevDayBtnDisabled: state.dateReducer.prevDayBtnDisabled,
  calendarVisible: state.dateReducer.calendarVisible,
  locale: state.dateReducer.locale,
  days: state.dateReducer.days,
  monthsShort: state.dateReducer.monthsShort,
});

const mapDispatchToProps = {
  updateBtnState,
  updateNewEventDate,
  updateCurrentDate,
  triggerCalendar,
};

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(onClickOutside(DatePicker)));
