import React, { Component } from 'react';
import PropTypes from 'prop-types';
import EventRow from '../../connected/EventRow';
import { roomsShape, eventsShape } from '../../../intl/shapes';
import './EventGroup.scss';

export default class EventGroup extends Component {
  static propTypes = {
    rooms: roomsShape.isRequired,
    events: eventsShape.isRequired,
    displayedDate: PropTypes.number.isRequired,
    displayedMonth: PropTypes.number.isRequired,
  }

  renderEventRows = () => {
    const {
      rooms, events, displayedDate, displayedMonth,
    } = this.props;

    return rooms.map((room) => {
      const neededEvents = events.filter(event => event.room.id === room.id
        && event.date.startingDate === displayedDate
        && event.date.startingMonth === displayedMonth);

      return (
        <EventRow
          room={room}
          events={neededEvents}
          key={room.id}
        />
      );
    });
  }

  render() {
    return (
      <div className="event-group">
        {this.renderEventRows()}
      </div>
    );
  }
}
