import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import onClickOutside from 'react-onclickoutside';
import Tooltip from '../Tooltip';
import { usersShape } from '../../../intl/shapes';
import './Event.scss';

class Event extends PureComponent {
  state = {
    tooltipOpened: false,
    tooltipTop: 0,
    tooltipLeft: 0,
  }

  static propTypes = {
    name: PropTypes.string.isRequired,
    roomName: PropTypes.string.isRequired,
    users: usersShape.isRequired,
    time: PropTypes.string.isRequired,
    date: PropTypes.string.isRequired,
    width: PropTypes.number.isRequired,
    offsetLeft: PropTypes.number.isRequired,
    openEditModal: PropTypes.func.isRequired,
    isMobile: PropTypes.bool.isRequired,
  }

  handleClickOutside = () => {
    this.setState({
      tooltipOpened: false,
    });
  }

  triggerTooltip = () => {
    this.setState(prevState => ({
      tooltipOpened: !prevState.tooltipOpened,
    }));
  }

  getTooltipPosition = (e) => {
    const rect = e.target.getBoundingClientRect();
    const x = e.clientX - rect.left - 220; // center relatively to the little triangle
    const y = e.clientY - rect.top;

    this.setState({
      tooltipTop: y,
      tooltipLeft: x,
    });
  }

  onEventClick = (e) => {
    this.triggerTooltip();
    this.getTooltipPosition(e);
  }

  render() {
    const {
      name, roomName, users, time, date, openEditModal, width, offsetLeft, isMobile,
    } = this.props;
    const { tooltipOpened, tooltipTop, tooltipLeft } = this.state;
    const position = { width: `${width}px`, marginLeft: `${offsetLeft}px` };
    const tooltipPosition = { top: `${tooltipTop}px`, left: `${tooltipLeft}px` };

    return (
      <div
        className={`event ${tooltipOpened ? 'event--selected' : ''}`}
        style={position}
        onClick={this.onEventClick}
      >
        {tooltipOpened
          && (
            <Tooltip
              style={!isMobile ? tooltipPosition : null}
              name={name}
              roomName={roomName}
              users={users}
              openEditModal={openEditModal}
              date={date}
              time={time}
              focus={tooltipOpened}
            />
          )
        }
      </div>
    );
  }
}

export default onClickOutside(Event);
