import React from 'react';
import FontAwesome from 'react-fontawesome';
import PropTypes from 'prop-types';
import RoundBtn from '../RoundBtn';
import './ArrowBtn.scss';

const ArrowBtn = ({ rotation, className, ...otherProps }) => {
  const renderArrowIcon = () => {
    if (rotation === 'left') {
      return (
        <FontAwesome
          name="chevron-left"
          style={{
            position: 'relative',
            right: '1px',
          }}
        />
      );
    }

    if (rotation === 'right') {
      return (
        <FontAwesome
          name="chevron-right"
          style={{
            position: 'relative',
            left: '1px',
          }}
        />
      );
    }
  };

  return (
    <RoundBtn
      className={`round-btn--arrow ${className}`}
      {...otherProps}
    >
      {renderArrowIcon()}
    </RoundBtn>
  );
};

ArrowBtn.propTypes = {
  onClick: PropTypes.func.isRequired,
  rotation: PropTypes.string.isRequired,
  className: PropTypes.string,
};

ArrowBtn.defaultProps = {
  className: '',
};

export default ArrowBtn;
