import React from 'react';
import { injectIntl, intlShape } from 'react-intl';
import Paragraph from '../Paragraph';
import { usersShape } from '../../../intl/shapes';
import './TooltipParticipators.scss';

const TooltipParticipators = ({ users, intl: { formatMessage } }) => {
  const firstUser = users[0];
  const remainingUsersLength = users.length - 1;
  const remainingParticipators = formatMessage(
    { id: 'tooltipParticipators.remainingParticipators' },
    { remainingUsersLength },
  );

  return (
    <div className="tooltip-participators">
      <div className="tooltip-participators__image-wrapper">
        <img
          src={firstUser.avatarUrl}
          alt={firstUser.login}
          className="tooltip-participators__image"
        />
      </div>

      <Paragraph>
        {firstUser.login}
        &nbsp;
        <span className="tooltip-participators__grey-text">
          {remainingParticipators}
        </span>
      </Paragraph>
    </div>
  );
};

TooltipParticipators.propTypes = {
  intl: intlShape.isRequired,
  users: usersShape.isRequired,
};

export default injectIntl(TooltipParticipators);
