import React, { Component } from 'react';
import { connect } from 'react-redux';
import { timeStripeArrayShape } from '../../../intl/shapes';
import Time from '../../ui/Time';
import './TimeLine.scss';

class TimeLine extends Component {
  static propTypes = {
    timeStripeArray: timeStripeArrayShape.isRequired,
  }

  renderTimeStripes = () => {
    const { timeStripeArray } = this.props;

    return timeStripeArray.map(timeStripe => (
      <Time
        hours={timeStripe}
        key={timeStripe}
        lessThanTwoDigitHours={timeStripe < 10}
      />
    ));
  }

  render() {
    const { timeStripeArray } = this.props;
    const lastInTimeArray = timeStripeArray.slice(-1)[0];
    const containerStyle = {
      width: `${lastInTimeArray * 100 + 105}px`, // no fucking idea...
    };

    return (
      <div
        className="timeline"
        style={containerStyle}
      >
        {this.renderTimeStripes()}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  timeStripeArray: state.mainContentReducer.timeStripeArray,
});

export default connect(mapStateToProps)(TimeLine);
