import React, { Component, createRef } from 'react';
import TimeLine from '../../connected/TimeLine';
import Events from '../../connected/Events';
import './TimeTable.scss';

class TimeTable extends Component {
  state = {
    clicked: false,
  }

  timeTable = createRef()

  componentDidMount() {
    this.move();
  }

  shouldUseDragging = () => {
    const isIE = !!window.MSInputMethodContext && !!document.documentMode;
    const isSafari = navigator.userAgent.indexOf('Safari') > -1
          && navigator.userAgent.indexOf('Chrome') === -1;

    return !isIE && !isSafari;
  }

  triggerClicked = () => {
    const { clicked } = this.state;
    const timeTable = this.timeTable.current;

    this.setState(prevState => ({
      clicked: !prevState.clicked,
    }));

    timeTable.style.cursor = clicked ? 'grab' : 'grabbing';
  }

  handleDrag = (e) => {
    const { clicked } = this.state;
    const timeTable = this.timeTable.current;

    e.preventDefault();

    if (clicked) {
      timeTable.scrollLeft = e.movementX < 0
        ? timeTable.scrollLeft + 20
        : timeTable.scrollLeft - 20;
    }
  }

  handleScroll = (e) => {
    const timeTable = this.timeTable.current;

    e.preventDefault();

    if (e.deltaY > 0) {
      timeTable.scrollLeft += 20;
    } else {
      timeTable.scrollLeft -= 20;
    }
  }

  move = () => {
    const timeTable = this.timeTable.current;

    if (this.shouldUseDragging()) {
      timeTable.addEventListener('mousedown', this.triggerClicked);
      timeTable.addEventListener('mouseup', this.triggerClicked);
      timeTable.addEventListener('mousemove', this.handleDrag);
    } else {
      timeTable.addEventListener('wheel', this.handleScroll);
    }
  }

  render() {
    return (
      <div className="time-table">
        <div
          className="time-table__inner"
          ref={this.timeTable}
        >
          <TimeLine />
          <Events />
        </div>
      </div>
    );
  }
}

export default TimeTable;
