import React from 'react';
import PropTypes from 'prop-types';
import Headline from '../Headline';
import Paragraph from '../Paragraph';
import EditBtn from '../EditBtn';
import TooltipParticipators from '../TooltipParticipators';
import { usersShape } from '../../../intl/shapes';
import './Tooltip.scss';

const Tooltip = ({
  name, roomName, date, time, users, openEditModal, style,
}) => (
  <div className="tooltip" style={style}>
    <EditBtn className="tooltip__edit-btn" onClick={openEditModal} />
    <Headline className="tooltip__headline">{name}</Headline>
    <Paragraph className="tooltip__paragraph">
      {date}
      ,
      {' '}
      {time}
      {' '}
      ·
      {' '}
      {roomName}
    </Paragraph>
    <TooltipParticipators users={users} />
  </div>
);

Tooltip.propTypes = {
  name: PropTypes.string.isRequired,
  roomName: PropTypes.string.isRequired,
  date: PropTypes.string.isRequired,
  time: PropTypes.string.isRequired,
  users: usersShape.isRequired,
  openEditModal: PropTypes.func.isRequired,
  style: PropTypes.shape({
    top: PropTypes.string,
    left: PropTypes.string,
  }),
};

export default Tooltip;
