import React, { Component } from 'react';
import { connect } from 'react-redux';
import RoomGroup from '../../ui/RoomGroup';
import { rearrangedRoomsShape } from '../../../intl/shapes';
import './Rooms.scss';

class Rooms extends Component {
  static propTypes = {
    rearrangedRooms: rearrangedRoomsShape.isRequired,
  }

  renderRoomGroups = () => {
    const { rearrangedRooms } = this.props;

    return rearrangedRooms.map(obj => (
      <RoomGroup
        rooms={obj.rooms}
        floor={obj.floor}
        key={obj.floor}
      />
    ));
  }

  render() {
    return (
      <div className="rooms">
        {this.renderRoomGroups()}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  rearrangedRooms: state.mainContentReducer.rearrangedRooms,
});

export default connect(mapStateToProps)(Rooms);
