import React from 'react';
import PropTypes from 'prop-types';
import Paragraph from '../Paragraph';
import './Time.scss';

const Time = ({ hours, lessThanTwoDigitHours }) => (
  <Paragraph
    className={`time ${lessThanTwoDigitHours ? 'time--with-offset' : ''}`}
  >
    {hours}
    :00
  </Paragraph>
);

Time.propTypes = {
  hours: PropTypes.number.isRequired,
  lessThanTwoDigitHours: PropTypes.bool.isRequired,
};

export default Time;
